import { applyMiddleware, createStore } from 'redux';
import thunk from 'redux-thunk';
import { composeWithDevTools } from 'redux-devtools-extension';
import rootReducer from '../reducers/rootReducer';

fetch('https://api.privatbank.ua/p24api/pubinfo?exchange&json&coursid=11')
  .then((response) => {
    if (!response.ok) {
      throw new Error(response.statusText);
    }
    return response;
  })
  .then(response => response.json())
  .then((currencyData) => {
    const currencySale = currencyData.map((currency) => {
      if (currency.ccy === 'EUR') {
        // console.log(Number(currency.sale));
        return Number(currency.sale);
      }
      return currency;
    });
    const usd = currencySale[0];
    const eur = currencySale[1];
    console.log(currencySale);
    // console.log(eur);
    return currencySale;
  })
  .then((currencySale) => {
    const USDSaleRate = currencySale[1];
    console.log(USDSaleRate);
    return currencySale;
  })
  .catch(() => { });

const store = createStore(rootReducer, composeWithDevTools(applyMiddleware(thunk)));

export default store;
